.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon {
  font-size: 2em;
  color: #6900ff;
}

.purple {
  color: #6900ff;
}

.title .purple {
  filter: drop-shadow(0px 0px 12px #18191b);
}
a {
  text-decoration: none;
  color: inherit;
}

@media screen and (max-width: 768px) {
  .title {
    font-size: 1.5em;
  }
}
